import React from "react";
import { Link } from "react-router-dom";
import bp from "../manage.blog.posts";
import "./Home.css";


export default class Home extends React.Component {

  componentDidMount() {
    document.title = "Kznovo";
  }

  render() {
    return(
      <React.Fragment>
        <p>
          Personal blog by Kazuya Hatta.<br/>
          I write about python, javascript, machine learning, webdev, career, and life.<br/>
          I am a Japanese.
          <p><Link to="/about">More about me</Link></p>
        </p>
        <br/>

        {bp.map((b, i) => (
          <div key={i} className="post">
            <h3><Link to={`/blog/${b.url}`}>{b.title}</Link></h3>
            <small>{new Date(b.date).toLocaleDateString("ja-JP")}</small>
            <p>{b.summary}</p>
          </div>
        ))}
        
      </React.Fragment>
    );
  }
}